@import url("https://fonts.googleapis.com/css?family=Montserrat:700|Roboto&display=swap");

:root {
  --mainRoyalBlue: rgb(46, 109, 223);
  --mainLightSteelBlue: #f5f6f7;
  --mainBlack: #222;
  --mainLightBlue: rgb(147, 176, 224);
  --mainPeru: #dd704e;
  --mainLightSkyBlue: #6bacf8;
}
* {
  margin: 0;
  box-sizing: border-box;
}
body {
  background: var(--mainLightBlue);
  color: var(--mainBlack);
  font-family: "Roboto", sans-serif;
}
h1 {
  font-family: "Montserrat", sans-serif;
  text-align: center;
  text-transform: uppercase;
  margin: 2rem 0;
  color: var(--mainLightSteelBlue);
  letter-spacing: 3px;
}
.App {
  margin: 0 auto;
  max-width: 1280px;
  width: 90%;
  background: var(--mainLightSteelBlue);
  padding-bottom: 2rem;
  padding-top: 1rem;
  border-radius: 15px;
}
.form-center {
  display: flex;
  padding: 0 1rem;
}
.form-group {
  padding: 1rem 0.75rem;
  flex: 0 0 50%;
}
label {
  display: block;
  color: var(--mainLightBlue);
  font-size: 1rem;
  text-transform: capitalize;
}

.form-control {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid var(--mainLightBlue);
  border-radius: 0;
  outline: none;
  height: 3rem;
  width: 100%;
  font-size: 16px;
  margin: 0 0 8px 0;
  padding: 0;
}
.btn {
  text-decoration: none;
  color: var(--mainLightSteelBlue);
  background-color: var(--mainRoyalBlue);
  text-align: center;
  letter-spacing: 0.5px;
  font-size: 14px;
  outline: 0;
  border: none;
  border-radius: 30px;
  display: block;
  height: 36px;
  line-height: 36px;
  padding: 0 16px;
  text-transform: uppercase;
  margin: 0 auto;
  cursor: pointer;
  box-shadow: 0 14px 28px rgb(46, 109, 223, 0.3),
    0 10px 10px rgb(46, 109, 223, 0.3);
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  display: flex;
  align-items: center;
}
.btn:hover {
  box-shadow: 0 12px 30px rgb(46, 109, 223, 0.4),
    0 10px 10px rgb(46, 109, 223, 0.5);
}
.btn-icon {
  margin-left: 0.75rem;
  font-size: 1.2rem;
}
.list {
  margin: 2rem 0.75rem 0 0.75rem;
  list-style-type: none;
  border: none;
  padding: 0;
}
.item {
  background-color: var(--mainLightSteelBlue);
  line-height: 1.5rem;
  padding: 12px 20px;
  margin-bottom: 1rem;
  /* border: 1px solid var(--mainLightBlue); */
  border: none;
  /* border-bottom: 1px solid var(--mainLightBlue); */
  border-radius: 15px;
  display: flex;
  justify-content: space-between;
  transition: all 0.3s linear;
}

.item:hover {
  box-shadow: 0 14px 28px rgb(147, 176, 224, 0.3),
    0 10px 10px rgb(147, 176, 224, 0.3);
  /* transform: scale(1.02); */
}
.info {
  flex: 0 0 60%;
  display: flex;
  justify-content: space-between;
}
.expense {
  margin-right: 2rem;
  color: var(--mainRoyalBlue);
  text-transform: capitalize;
}
.amount {
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  font-size: 1rem;
  /* color: var(--mainLightSteelBlue); */
  color: var(--mainRoyalBlue);
  border-radius: 2px;
  padding: 1px 5px;
}

.clear-btn,
.edit-btn {
  background: none;
  border: none;
  font-size: 1.2rem;
  outline: none;
  cursor: pointer;
}
.clear-btn {
  color: var(--mainPeru);
}
.edit-btn {
  color: var(--mainLightSkyBlue);
}

.total {
  font-weight: 300;
  color: var(--mainLightSteelBlue);
  background-color: var(--mainRoyalBlue);
  border-radius: 2px;
  padding: 2px 5px;
}
.alert {
  padding: 0.5rem 1.25rem;
  color: var(--mainLightSteelBlue);
  text-align: center;
  text-transform: capitalize;
  width: 90%;
  margin: 2rem auto 0 auto;
  border-radius: 2px;
  text-transform: capitalize;
}
.alert-danger {
  background: var(--mainPeru);
}
.alert-success {
  background: var(--mainRoyalBlue);
}
@media screen and (min-width: 601px) {
  .App,
  .list,
  .alert {
    width: 85%;
  }

  .list {
    margin: 2rem auto 0 auto;
  }
}
@media screen and (min-width: 993px) {
  .App,
  .list,
  .alert {
    width: 70%;
  }
  .list {
    margin: 2rem auto 0 auto;
  }
  .info {
    flex: 0 0 50%;
  }
}
